<script lang="ts">
import { defineComponent, h, provide, PropType } from 'vue'
import SkeletonLayout from './SkeletonLayout.vue'
import {
  paragraphDefaultRow,
  AVATAR_SHAPE_NAMES,
  BUTTON_SHAPE_NAMES,
  ButtonShapeNames,
  AvatarShapeNames
} from '@/hooks/skeleton'
import { createEnumsValidator } from '@/helpers/validator'

export default defineComponent({
  name: 'fx-skeleton',
  props: {
    // 
    loading: {
      type: Boolean,
      default: true
    },
    // 
    avatar: {
      type: Boolean,
      default: false
    },
    // 
    animated: {
      type: Boolean,
      default: false
    },
    // 
    avatarShape: {
      type: String as PropType<AvatarShapeNames>,
      validator: createEnumsValidator(AVATAR_SHAPE_NAMES),
      default: null
    },
    // 
    buttonShape: {
      type: String as PropType<ButtonShapeNames>,
      validator: createEnumsValidator(BUTTON_SHAPE_NAMES),
      default: null
    },
    // 
    paragraphRow: {
      type: Number,
      default: paragraphDefaultRow
    }
  },
  setup(props, { slots }) {
    provide('fxSkeletonProps', props)

    return () => {
      if (!props.loading) {
        return slots.default ? slots.default() : null
      }

      return h(
        SkeletonLayout,
        {
          animated: props.animated,
          avatar: props.avatar
        },
        slots.layout
      )
    }
  }
})
</script>
