import { isDate, isObject } from '@/helpers/util'
import { parseParamsByRules } from '@/apis/rules'
import { ApiOptions } from './types'

export function createStorage(areaName: string, options?: ApiOptions) {
  if (!options || !isObject(options)) {
    options = {} as ApiOptions
  }

  const { areaName: _areaName, perLimitSize, limitSize } = parseParamsByRules(
    Object.assign({ areaName }, options),
    'createStorage'
  ) as {
    areaName: string
    perLimitSize: number
    limitSize: number
  }

  const prefix = _areaName + ':'

  /**
   *  key 
   * @param key
   */
  function getStorage(key: string) {
    parseParamsByRules(
      {
        key
      },
      'getStorage'
    )

    const value = localStorage.getItem(prefix + key)

    if (value) {
      try {
        const ret = JSON.parse(value)

        if (ret.isApp) {
          return ret.isDate ? new Date(ret.data) : ret.data
        }
      } catch (e) {
        //
      }

      return null
    }
  }
  /**
   *  key  key  key  1024KB 2560KB
   * @param key
   * @param data
   */
  function setStorage(key: string, data: any) {
    parseParamsByRules(
      {
        key,
        data
      },
      'setStorage'
    )

    const storageInfo = getStorageInfo()

    const value = JSON.stringify({
      isDate: isDate(data),
      data
    })

    const valueSize = value.length / 1024

    if (valueSize > perLimitSize) {
      throw new Error(
        `Storage error: per storage limit size exceeded ${perLimitSize}KB.`
      )
    }

    if (storageInfo.currentSize + valueSize > storageInfo.limitSize) {
      // 
      throw new Error(
        `Storage error: total storage limit size exceeded ${limitSize}KB.`
      )
    }

    localStorage.setItem(prefix + key, value)
  }
  /**
   *  key
   * @param key
   */
  function removeStorage(key: string) {
    parseParamsByRules(
      {
        key
      },
      'removeStorage'
    )

    localStorage.removeItem(prefix + key)
  }
  /**
   * 
   */
  function clearStorage() {
    // localStorage.clear()

    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i) as string

      if (key.indexOf(prefix) === 0) {
        // api
        localStorage.removeItem(key)
      }
    }
  }
  /**
   * storage
   */
  function getStorageInfo() {
    const keys = []
    let size = 0

    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i) as string

      if (key.indexOf(prefix) === 0) {
        // api
        keys.push(key.replace(prefix, ''))
      }

      size += (localStorage.getItem(key) as string).length
    }

    return {
      keys,
      currentSize: size / 1024,
      limitSize
    }
  }

  return {
    getStorage,
    setStorage,
    removeStorage,
    clearStorage,
    getStorageInfo
  }
}
