<template>
  <div class="fx-empty">
    <slot name="image" v-if="$slots.image"></slot>
    <template v-else>
      <img class="fx-empty_image" :src="imageUrl" />
    </template>
    <p class="fx-empty_description" v-show="description">
      {{ description }}
    </p>
    <slot></slot>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType } from 'vue'
import { createEnumsValidator, getEnumsValue } from '@/helpers/validator'

const TYPE_NAMES = [
  'default',
  'error',
  'network',
  'search',
  'permission',
  'service'
]

export default defineComponent({
  name: 'fx-empty',
  props: {
    // 
    description: {
      type: String,
      default: ''
    },
    // 
    type: {
      type: String as PropType<
        'default' | 'error' | 'network' | 'search' | 'permission' | 'service'
      >,
      validator: createEnumsValidator(TYPE_NAMES),
      default: TYPE_NAMES[0]
    }
  },
  setup(props) {
    const imageUrl = computed(() => {
      return `https://cdn.fox2.cn/vfox/empty/${getEnumsValue(
        TYPE_NAMES,
        props.type
      )}@3x.png`
    })

    return {
      imageUrl
    }
  }
})
</script>
